import RangeSlider from 'vue-range-slider'
import 'vue-range-slider/dist/vue-range-slider.css'

import VueSlickCarousel from 'vue-slick-carousel'
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'
export default {
  name: 'IndexPage',
  components: { VueSlickCarousel, RangeSlider },
  data() {
    return {
      musicArr: [
        {id: 1, title: 'Emberz - Embers', sound: require('@/static/music/embers.mp3').default},
        {id: 2, title: '告五人Accusefive 經典歌曲盒輯', sound: require('@/static/music/告五人Accusefive.mp3').default},
      ],
      playTitle: '',
      isPlay: false,
      audio: null,
      duration: 100,
      currentTime: 0,
      isSpin: false,
      gainNode: null,
      currentItem: 0
    }
  },
  methods: {
    handleSwipe(oldSlideIndex, newSlideIndex) {
      this.isPlay = false
      this.isSpin = false
      this.currentTime = 0
      if (this.audio) {
        this.audio.pause()
      }
      this.audio = null
      this.currentItem = newSlideIndex
      this.playTitle = this.musicArr[newSlideIndex].title
    },
    changeVolume() {
      if (this.audio) this.audio.volume = this.gainNode
    },
    changeDuration() {
      if (this.audio) this.audio.currentTime = this.currentTime
    },
    playNotification(sound) {
      this.isSpin = !this.isSpin
      if (this.isPlay) {
        this.isPlay = false
        this.audio.pause()
      } else {
        if (this.audio) {
          this.isPlay = true
          this.audio.play()
        } else {
          this.isPlay = true
          this.audio = new Audio(sound)
          this.audio.volume = 0.5
          this.gainNode = this.audio.volume
          
          this.audio.play()
          setTimeout(() => {
            this.duration = parseInt(this.audio.duration)
          }, 200)

          const playerTimer = setInterval(() => {
            if (this.audio) {
              this.currentTime = parseInt(this.audio.currentTime)
            } else {
              clearInterval(playerTimer)
            }
            if (this.currentTime === this.duration) {
              this.isPlay = false
              this.isSpin = false
              clearInterval(playerTimer)
            }
          }, 1000)
        }
      }
    },
    changeMusic(bool) {
      if (bool) {
        this.$refs.carousel.next()
      } else {
        this.$refs.carousel.prev()
      }
    }
  },
  mounted() {
    this.playTitle = this.musicArr[0].title
  }
}
